<template>
  <div>
    <div id="map" ref="map"></div>
    <search
      v-show="isOpenSearch"
      @typeEntidad="typeEntidad"
      @onSearch="onSearch"
      :infoListDirections="infos"
    ></search>
  </div>
</template>

<script>
import axios from "axios";
import Search from "../../components/Search";

export default {
  name: "MapIndex",
  inject: ["notyf"],
  components: { Search },
  props: ["infoListDirections"],
  data: () => ({
    isLoading: false,
    isOpenSearch: true,
    markers: [],
    infos: [],
    markerClusterer: "",
    myLocation: "",
    dataDistrito: [],
  }),
  mounted() {
    this.addMap();
    this.getLocation();
  },
  methods: {
    getLocation() {
      let ctx = this;
      navigator.geolocation.getCurrentPosition(function (pos) {
        let crd = (ctx.myLocation = pos.coords);

        ctx.location =
          `Latitude : ${crd.latitude}` +
          `Longitude: ${crd.longitude}` +
          `More or less ${crd.accuracy} meters.`;
        ctx.map.setCenter({ lat: crd.latitude, lng: crd.longitude });

        let image = require("../../assets/icons/marker-legal-help.png");

        new window.google.maps.Marker({
          position: {
            lat: parseFloat(crd.latitude),
            lng: parseFloat(crd.longitude),
          },
          //label: {text: 'USTED ESTA AQUI', fontWeight: 'bold', color: 'red'},
          map: ctx.map,
          icon: { url: image, labelOrigin: { x: 22, y: 55 } },
          //collisionBehavior: window.google.maps.CollisionBehavior.REQUIRED_AND_HIDES_OPTIONAL,
        });

        var loc = {};
        var geocoder = new window.google.maps.Geocoder();
        loc.lat = parseFloat(crd.latitude);
        loc.lng = parseFloat(crd.longitude);

        var latlng = new window.google.maps.LatLng(loc.lat, loc.lng);
        geocoder.geocode({ latLng: latlng }, function (results, status) {
          // console.log(results);
          console.log(status);
          // console.log(results[0]["formatted_address"]);

          const queue = [];

          for (let i = 0; i < results.length; i++) {
            if (results[i]["formatted_address"].indexOf("+") != -1) {
              //0: {formatted_address: '3255+P8 San Juan de Lurigancho, Perú'}
              let address_ = results[i]["formatted_address"]
                .substring(8)
                .split(",");
              queue.push({ formatted_address: address_[0] });
            }
          }
          //INGRESAMOS EL DISTRITO A LA FUNCION, PARA QUE PROCESE LOS IDs
          ctx.defaultDistrito(queue[0].formatted_address);
        });

        // var map = new window.google.maps.Map(document.getElementById('map'), {
        //     center: {lat: crd.latitude, lng: crd.longitude},
        //     zoom: 6
        // });
        // var infoWindow = new window.google.maps.InfoWindow({map: map});

        //  console.log(infoWindow)
        /*
        let info = new window.google.maps.InfoWindow({
          content: 'USTED ESTA AQUI',
        });

        marker.addListener('click', () => {
          info.open(ctx.map, marker)
        })*/
      });
    },
    addMap() {
      this.map = new window.google.maps.Map(this.$refs["map"], {
        center: {
          lat: -12.078284799999999,
          lng: -77.0441216,
        },
        //ZOMM DEL MAPA 18 NORMAL, 9 MAS LEJANO
        zoom: 10,
        styles: [
          {
            featureType: "poi.business",
            stylers: [{ visibility: "off" }],
          },
          {
            featureType: "transit",
            elementType: "labels.icon",
            stylers: [{ visibility: "off" }],
          },
        ],
      });
      //this.geocoder = new window.google.maps.Geocoder();
    },

    defaultDistrito(district) {
      //CARGAR AUMATICO NOTARIA
      //PARA ESTA LOGICA SOLO SERA PARA LA PROVINCIA DE LIMA - AUTOMATICO
      let url = "listDistrict?name_district=" + district;
      axios
        .get(url)
        .then((response) => {
          //OBTENEMOS TODOS LOS DATOS DEL DISTRITO CON DEPARTAMENTO, PROVINCIO Y DISTRITO
          let locx = {};
          locx.department_id = response.data[0].department_id;
          locx.province_id = response.data[0].province_id;
          locx.district_id = response.data[0].id;
          locx.site_id = "";
          locx.type = "notary";

          this.dataDistrito = response.data[0];

          this.addMarkers(locx);
        })
        .catch()
        .then(() => {});
    },
    addMarkers(options = null) {
      let image = require("../../assets/icons/marker.png");
      let url = "sites";
      let config = { params: options };
      // console.log(config);
      axios
        .get(url, config)
        .then((response) => {
          this.sites = response.data;
          let markers = [];
          let infos = [];

          if (this.sites.length === 0) {
            this.notyf.success(
              "Se encontraron 0 resultados para tu busqueda, intenta con otros parametros."
            );
          }

          if (this.sites.length > 0) {
            this.notyf.success(
              "Se encontraron " +
                this.sites.length +
                " resultados para tu busqueda."
            );
          }

          this.sites.forEach((site, index) => {
            markers[index] = new window.google.maps.Marker({
              position: {
                lat: parseFloat(site.latitude),
                lng: parseFloat(site.longitude),
              },
              label: { text: site.name, fontWeight: "bold" },
              map: this.map,
              icon: { url: image, labelOrigin: { x: 16, y: 42 } },
              //collisionBehavior: window.google.maps.CollisionBehavior.OPTIONAL_AND_HIDES_LOWER_PRIORITY,
            });

            let from =
              this.myLocation.latitude + "," + this.myLocation.longitude;
            let to = site.latitude + "," + site.longitude;

            let scheduler =
              site.schedule === null ? "No registrado" : site.schedule;
            infos[index] = new window.google.maps.InfoWindow({
              
              directions: site.address, //AGREGO LAS DIRECCIONES LISTA
              directions_phone: site.phone,
              directions_name: site.name,
              directions_from: from,
              directions_to: to,
              directions_scheduler: scheduler,

              content:
                site.name +
                "<br>Telefono: " +
                site.phone +
                "<br>Direccion: " +
                site.address +
                "<br>Horario: " +
                scheduler +
                '<br><a target="_blank" href="https://www.google.com/maps/dir/' +
                from +
                "/" +
                to +
                '/am=t">Ver como llegar</a>',
            });

            markers[index].addListener("click", () => {
              infos[index].open(this.map, markers[index]);
            });
          });

          this.markers = markers;
          this.infos = infos; //RECOLECTA LAS DIRECCIONES
          this.markerClusterer = new window.MarkerClusterer(this.map, markers, {
            imagePath:
              "https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m",
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //SE EJECUTA CUANDO SE PRESIONA EL TAB
    typeEntidad(form) {
      this.removeAllMarkers();
      //this.geocodeAddress(site.address, this.geocoder, this.map);
      //SE RECOJE LOS DATOS QUE SE LLENARON POR PRIMERA VEZ EN EL REFRESH
      let locx = {};
      locx.department_id = this.dataDistrito.department_id;
      locx.district_id = this.dataDistrito.id;
      locx.province_id = this.dataDistrito.province_id;
      locx.site_id = "";
      locx.type = form.type;
      this.addMarkers(locx); //Permite el Pintado y el Listado
    },
    
    onSearch(form) {
      this.removeAllMarkers();
      this.addMarkers(form);
      //this.geocodeAddress(site.address, this.geocoder, this.map);
    },
    geocodeAddress(address, geocoder, resultsMap) {
      //let address = 'Av. Guardia Chalaca 2176, Bellavista 07016';
      address = address + ",peru  ";
      geocoder.geocode({ address: address }, (results, status) => {
        if (status === "OK") {
          resultsMap.setCenter(results[0].geometry.location);
          new window.google.maps.Marker({
            map: resultsMap,
            position: results[0].geometry.location,
          });
        } else {
          alert(
            "Geocode was not successful for the following reason: " + status
          );
        }
      });
    },
    removeAllMarkers() {
      for (let i = 1; i < this.markers.length; i++) {
        this.markers[i].setMap(null);
      }

      for (let i = 1; i < this.infos.length; i++) {
        this.infos[i].setMap(null);
      }

      if (typeof this.markerClusterer === "object") {
        this.markerClusterer.clearMarkers();
      }
    },

  },
};
</script>
<style lang="scss">
#map {
  height: calc(100vh - 56px);
}
</style>